.autocomplete {
  width: 300px;
  height: 300px;
  padding: 30px; }
  .autocomplete__input {
    width: 300px;
    height: 40px;
    padding: 3px;
    font-size: 17px; }
    .autocomplete__input_focused {
      border: 2px solid green; }
  .autocomplete__datalist {
    margin-top: 5px;
    height: 100px;
    overflow-x: auto;
    width: 300px;
    display: none; }
    .autocomplete__datalist_show {
      display: block; }
    .autocomplete__datalist__item {
      font-size: 17px;
      cursor: pointer; }

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }



